export default {
	
	name: "trailerStars",
	
	props:{
		innerScore: 0,
		showNum: 0,
	},
	
	data() {
		return {
			yellowScore: 0,
		};
	},
	//生命周期组件，创建完成后调用
	created() {
		if (this.innerScore != null && this.innerScore != undefined && this.innerScore != '') {
			var tempScore = this.innerScore;
		}
		this.yellowScore = parseInt(tempScore/2);
	}
}
